middle:'middle',
}
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          

that.changeFixed(this.clientHeight)
},
created () {
this.params.carType = this.$route.query.carType || null
this.params.type = this.$route.query.type || null
this.params.catId = this.$route.query.catId || null
this.params.mid = this.$route.query.mid || null

this.params04.sessionId = this.params03.sessionId = this.params02.sessionId = this.params.sessionId = getCookie('sessionId')
this.getFreeQuestionList()
this.getExamInfo()
this.timer()
},
methods: {
timer: function () {
if (this.time > 0) {
this.time--;

var m = '';
var s = '';
m = Math.floor(this.time/60%60);
m < 10&&(m='0'+m);
s = Math.floor(this.time%60);
s < 10&&(s='0'+s);
this.btntxt = m + ':' + s

setTimeout(this.timer, 1000);
} else {
this.time = 0;
this.btntxt = "获取验证码";
this.disabled = false;
}
},
doAgain(){







if(this.automatic){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
this.reGetQuestionList()
}else {
this.reGetQuestionList()
}

},
changeFixed(clientHeight) {                        //动态修改样式

this.$refs.homePage.style.height = clientHeight + 'px';

},

getFreeQuestionList (e){
const that = this;
Drive.prototype.getFreeQuestionList(that.params).then(res => {
let list = res.data.data || [];
that.questionList = list.resultList
that.question = that.questionList[that.questionIndex]
that.music = that.question.mp3
that.count = list.count
that.setquestionType()
console.log(that.question)
});
},

reGetQuestionList (e){
const that = this;
Drive.prototype.reGetQuestionList(that.params04).then(res => {
let list = res.data.data || [];
that.questionIndex = 0
that.questionList = list.resultList
that.question = that.questionList[that.questionIndex]

that.music = that.question.mp3
that.count = list.count
that.setquestionType()
console.log(that.question)
});
},
setquestionType(){
const that = this
console.log('setquestionType')
for(var idx in that.question.optionList){
that.question.optionList[idx].key = that.question.optionList[idx].id
that.question.optionList[idx].value = that.question.optionList[idx].optionContent
}

if(that.question.type == 3){
if(that.question.isRight != 0){
for(var idx in that.question.optionList){
if(that.question.optionList[idx].id == that.question.chooseId){
that.choosed = idx
break
}
}
}else {
that.choosed = 3
}

}

if(that.question.type == 1){
if(that.question.isRight != 0){
for(var idx in that.question.optionList){
if(that.question.optionList[idx].id == that.question.chooseId){
that.radioValue.push(that.question.optionList[idx].id)
break
}
}
}else {
that.radioValue = []
}
console.log(that.choosed)
}
if(that.question.type == 2){
if(that.question.isRight != 0){
that.radioValue = that.question.choosed.split(';')






}else {
that.radioValue = []
}
console.log(that.choosed)
}
if(that.question.type == 1){
that.questionType = '单选题'
}
if(that.question.type == 2){
that.questionType = '多选题'
}
if(that.question.type == 3){
that.questionType = '判断题'
}
},
getExamInfo (e){
const that = this;
Drive.prototype.getExamInfo(that.params02).then(res => {
let list = res.data.data || {};
that.examInfo = list

});
},
onQuestionTap (item,index){
console.log(index)
const that = this
console.log(this.questionList)
that.radioOn = false
this.questionIndex = index
this.question = this.questionList[index]
that.setquestionType()

if(this.audio.playing){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
},
judgeQuestion (e){
const that = this;





















Drive.prototype.judgeQuestion(that.params03).then(res => {
let list = res.data.data || {};

that.questionList = list.examDto.questionDtoList
that.choosed = 3
that.radioValue = []
if(that.questionList[that.questionIndex].isRight == 2){
that.onSubmitTap()
}else if(!e){
++that.questionIndex
console.log('this.automatic' + this.automatic)
if(!this.audio.playing && !this.automatic){
this.nextLoading = false
this.playHelpMp3 = false
}
if(that.questionIndex == that.count){
--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.setquestionType()
that.showTips()
}else {
that.question = that.questionList[that.questionIndex]
that.setquestionType()
if(this.automatic){
that.nextLoading = true
}
that.audio.playing = false
if(this.audioStyle == 2){
that.music = this.question.allMp3
}else {
that.music = this.question.mp3
}
}
}


console.log(that.question)
});
},
onChooseTap (e){









const that = this;
that.choosed = e

that.params03.index = that.questionIndex;  
that.params03.qid = that.question.id;           
that.params03.optionId = that.question.optionList[e].id;      


if(this.question.isRight == 0){
this.judgeQuestion()
}else {

that.showPositionValue = true
that.toast = '不能对之前已答过的题重复答题'
}

},
change (e){
console.log('change')
const that = this
that.params03.index = that.questionIndex;  
that.params03.qid = that.question.id;           


if( that.radioValue.length != 0 && that.radioOn){
if(that.question.type == 1){
that.params03.optionId = that.radioValue[0]
if(that.question.isRight == 0){
this.judgeQuestion()
} else {

that.showPositionValue = true
that.toast = '不能对之前已答过的题重复答题'
}
}
if(that.question.type == 2){
if(that.question.isRight != 0){
that.showPositionValue = true
that.toast = '不能对之前已答过的题重复答题'
}
}
}else {
if(that.radioValue.length != 0){
that.radioOn = !that.radioOn
}
}
},
processMultipleselection(){
var str = ''
this.radioValue.forEach(item => {
console.log(item)
str += `${item}`;
str += ';';
});
return str.slice(0,-1);
},
onNextTap (){
console.log('onNextTap')
const that = this
if(that.count > that.questionIndex+1){
that.question = that.questionList[that.questionIndex+1]
that.questionIndex++
that.setquestionType()
if(this.automatic){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
}else {


}
},
onLastTap(){
const that = this
if(that.question.isRight != 0){
switch(that.question.type)
{
case 1:

--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.setquestionType()
if(this.automatic){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
break;
case 2:

--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.setquestionType()
if(this.automatic){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
break;
case 3:

--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.setquestionType()
if(this.automatic){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
break;
}
}else if(that.questionIndex > 0){
--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.radioOn = false
that.setquestionType()
if(this.audio.playing){
this.nextLoading = true
this.pausePlay()
this.audio.playing = false
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}else {
if(that.audioStyle == 2){
that.music = that.question.allMp3
}else {
that.music = that.question.mp3
}
}
console.log(that.question)
}else {
console.log('first one')
}

},
onCheckTap(){
const that = this
that.popup = true
that.radioOn = false
this.getExamInfo()
},
showTips(){
const that = this
that.noFinish = 0
for(var idx in that.questionList){
if(that.questionList[idx].isRight == 0){
++that.noFinish
}
}
that.showTipsImg = false
if(that.noFinish > 0){
var num = that.count - that.noFinish
that.tips = '您还有' + `${that.noFinish}` + '没有完成'
that.answer = '是否确认交卷？'
that.cancel = '再想想'
that.congfirm= '立即交卷'
}else {





that.tips = '提示'
that.answer = '是否结束体验'
that.cancel = '取消'
that.congfirm='确认'
}








that.show = true
},
onHidePopupTap(){
console.log('hide')
this.popup = false
this.radioOn = true
},
Popuphide(){
console.log('hide')
this.radioOn = true
},
onSubmitTap(){
const that = this
that.showcancel = true
that.tips = ''
that.answer = `${that.question.helpContent}`
that.cancel = '重听'
that.congfirm= '关闭'
that.show = true
this.finish = false
this.ifcancel = false
console.log('helpMp3' + this.audio.playing)
that.playHelpMp3 = true
if(this.audio.playing){
this.nextLoading = false
this.pausePlay()
this.audio.playing = false
that.music = that.question.helpMp3
}else {
this.nextLoading = false

this.audio.playing = false
that.music = that.question.helpMp3
this.audio.playing ? this.pausePlay() : this.startPlay()
}





















},
onCancel () {
console.log('on cancel')
this.showTipsImg = true
if(this.cancel != '再想想'){
this.ifcancel = false
if(!this.audio.playing){
this.audio.playing ? this.pausePlay() : this.startPlay()
}
}else {
this.ifcancel = true
}
},
onConfirm (msg) {
console.log('on confirm')
const that = this
if(this.audio.playing){

this.pausePlay()
this.audio.playing = false

}else {
this.audio.playing = false
this.pausePlay()

}
this.showTipsImg = true

},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
},




startPlayOrPause() {
console.log('startPlayOrPause')
const that = this


if(that.audioStyle == 2){

if(this.audio.playing){
this.automatic = false
this.playHelpMp3 = false
this.nextLoading = false
}else {
this.automatic = true
this.playHelpMp3 = true
}
}else {

if(this.audio.playing){
this.automatic = false
this.playHelpMp3 = false
this.nextLoading = false
}else {
this.automatic = true
this.playHelpMp3 = true
}
}

return this.audio.playing ? this.pausePlay() : this.startPlay()
},
readTopic (){
const that = this
if(!this.audio.playing){


this.music = this.question.mp3
this.audioStyle == 1

return this.audio.playing ? this.pausePlay() : this.startPlay()
}
},
readTopicAdd (){
const that = this
if(!this.audio.playing){
that.playHelpMp3 = true

this.music = this.question.allMp3
console.log(this.music)

this.audioStyle = 2
return this.audio.playing ? this.pausePlay() : this.startPlay()

}
},
upgadePlayOrPause(){
console.log('upgadePlayOrPause')
this.audioStyle = 2
this.music = this.question.allMp3
return this.audio.playing ? this.pausePlay() : this.startPlay()
},

onError () {
console.log('onError');
this.audio.waiting = true
},

startPlay() {
console.log('startPlay');
this.$refs.audio.play()
},

pausePlay() {
console.log('pausePlay');
this.$refs.audio.pause()
},

onPause () {











console.log('onPause');
const that = this;
var url = ''

if(this.automatic){
console.log('接着下一首')











this.audio.playing = false

}else {

this.audio.playing = false

}

},

onWaiting (res) {
console.log('onWaiting');
console.log(res)
},
onPlaying (res){
console.log('onPlaying')
console.log(res)
if(this.nextLoading){
this.nextLoading = !this.nextLoading
}
},

onPlay (res) {
console.log('onPlay')
this.audio.playing = true
this.audio.loading = false
if(!this.controlList.onlyOnePlaying){
return
}
let target = res.target
let audios = document.getElementsByTagName('audio');
[...audios].forEach((item) => {
if(item !== target){
item.pause()
}
})
},

onTimeupdate(res) {

console.log('onTimeupdate')
this.audio.currentTime = res.target.currentTime
this.sliderTime = parseInt(this.audio.currentTime / this.audio.maxTime * 100)
},


onLoadedmetadata(res) {
console.log('loadedmetadata')
console.log(res)


const that = this
this.audio.waiting = false
this.audio.maxTime = parseInt(res.target.duration)
console.log('that.nextLoading' + that.nextLoading)

if(that.nextLoading){
that.startPlay()
}else if(that.playHelpMp3){
that.startPlay()
that.playHelpMp3 = false
}
},
onSubTap(e){








if(this.radioValue.length > 0 && this.question.isRight == 0){
this.params03.optionId = this.processMultipleselection()
this.judgeQuestion()
}
}
}
}



